        <h3>推荐歌单</h3>
        <el-col :span="8" v-for="(item, index) in sixPersonalized" :key="index" class="box-col">
          <SongList v-bind:item="item"></SongList>
        </el-col>
      </el-row>
    </div>
    <div class="newsong">
      <h3>最新单曲</h3>
      <li v-for="(item, index) in ten" :key="index">
