<script>
// @ is an alias to /src
import SongList from "@/components/SongList.vue";
import NewSong from "@/components/NewSong.vue";

export default {
  name: 'home',
  components: {
    SongList,
    NewSong
  },
  data() {
  return {
    personalized:[],
    newsong:[]
    }
  },
  computed: {
    sixPersonalized: function() {
      return this.personalized.slice(0, 6);
    },
    ten: function() {
      return this.newsong.slice(0, 20);
    }
  },
  methods: {
    
  },
  created() {
    // this.$store.commit("updateFullscreenLoadingTrue");
    axios
    .get('personalized')
    .then(res => {
        // console.log(res.data.result);
        this.personalized = res.data.result;
      })
      .catch(err => {
        console.error(err);
      });
      axios
    .get('top/song?type=96')
    .then(res => {
        console.log(res.data.data);
        this.newsong = res.data.data;
        // this.$store.commit("updateFullscreenLoadingFalse");
      })
      .catch(err => {
        console.error(err);
      });
  // axios
  //   .get('/banner?type=2')
