<template>
    <div class="SongList" @click="handle(item)">
        <img :src="item.picUrl" alt="">
        <p>{{item.name}}</p>
    </div>
</template>
<script>
export default {
  props: ["item"],
    methods: {
      handle: function(params) {
      console.log(params);
      this.$router.push({ path: "music", query: { id: params.id } });
    }
    },
}
</script>
<style lang="less">
    .SongList{
        height: 200px;
        img{
            width:100%;
        }
        p{
            margin: 0;
            font-size: 14px;
            text-align: left;
        }
    }
</style>