<template>
  <div class="player" v-if="currentPlay" :views="show()">
    <el-row>
      <el-col :span="6">
        <div class="img">
          <!-- <img v-if="currentPlayDetail.album.artist" :src="currentPlayDetail.album.artist.picUrl" alt> -->
          <img v-if="currentPlayDetail.album" :src="currentPlayDetail.album.picUrl" :onerror="currentPlayDetail.album.artist.picUrl">
          <img v-else-if="currentPlayDetail.al.picUrl" :src="currentPlayDetail.al.picUrl" alt>
          <img v-else :src="currentPlayDetail.album.artist.picUrl" alt>
        </div>
      </el-col>
      <el-col :span="18">
        <div class="Name">
          <p v-if="currentPlayDetail.name">{{currentPlayDetail.name}}</p>
          <p v-else>{{currentPlayDetail.album.name}}</p>
        </div>
        <div class="artist">
          <!-- <p v-if="currentPlayDetail.artist.name">{{currentPlayDetail.artist.name}}</p> -->
          <p v-if="currentPlayDetail.artist">{{currentPlayDetail.artists[0].name}}</p>
          <p></p>
        </div>
        <audio :src="currentPlay.url" autoplay="autoplay" v-on:ended="end"></audio>
        <div class="audio">
          <el-row :gutter="24">
            <el-col :span="8"><img class="back" src="../../上一首.svg"  @click="back" alt=""></el-col>
            <el-col :span="8">
              <img v-if="$store.state.isplaying" class="bofang" src="../../暂停.svg" @click="play" alt="">
              <img v-else class="bofang" src="../../播放.svg" @click="play" alt="">
            </el-col>
            <el-col :span="8"><img class="next" src="../../下一首.svg" @click="next"></el-col>
          </el-row>
        </div>
        
      </el-col>
    </el-row>
    
    
  </div>
</template>

<script>
export default {
  computed: {
    currentPlay: function() {
      return this.$store.state.currentPlay;
    },
    currentPlayDetail: function() {
      return this.$store.state.currentPlayDetail;
    },
    currentPlayList: function() {
      return this.$store.state.currentPlayList;
    },
  },
  methods: {
    end(){
      // next();
      var currentPlayId = this.currentPlay.id;
      var currentIndex;
      this.currentPlayList.forEach(function(element, index) {
        if (element.id == currentPlayId) {
          currentIndex = index;
        }
      });
      var nextPlayId = this.currentPlayList[currentIndex + 1].id;
      console.log(nextPlayId);
      this.$store.commit(
        "updateCurrentPlayDetail",
        this.currentPlayList[currentIndex + 1]
      );
      this.getSongDetail(nextPlayId);
      console.log(this.currentPlayList)
      var audio=document.querySelector('audio')
    //   audio.addEventListener('ended', function () {
    //     next();
    // }, false);
    },
    show(){
      var body=document.querySelector('body')
      body.style.padding='0 0 100px 0'
    },
    move(){
      var audio=document.querySelector('.player')
      this.$router.replace('music')
      audio.style.opacity='0'
    },
    play(){
      var audio=document.querySelector('audio')
      if (this.$store.state.isplaying) {
        audio.pause()
        this.$store.state.isplaying = false;
      }else{
        audio.play();
        this.$store.state.isplaying = true;
      }
    },
    getSongDetail: function(parmas) {
      axios
        .get("/song/url", {
          params: {
            id: parmas
          }
        })
        .then(res => {
          // console.log(res.data.data[0]);
          this.$store.commit("updateCurrentPlay", res.data.data[0]);
        })
        .catch(err => {
          console.error(err);
        });
    },




    next() {
      var currentPlayId = this.currentPlay.id;
      var currentIndex;
      this.currentPlayList.forEach(function(element, index) {
        if (element.id == currentPlayId) {
          currentIndex = index;
        }
      });
      var nextPlayId = this.currentPlayList[currentIndex + 1].id;
      console.log(nextPlayId);
      this.$store.commit(
        "updateCurrentPlayDetail",
        this.currentPlayList[currentIndex + 1]
      );
      this.getSongDetail(nextPlayId);
      console.log(this.currentPlayList)
    },
    back() {
      var currentPlayId = this.currentPlay.id;
      var currentIndex;
      this.currentPlayList.forEach(function(element, index) {
        if (element.id == currentPlayId) {
          currentIndex = index;
        }
      });
      var nextPlayId = this.currentPlayList[currentIndex - 1].id;
      console.log(nextPlayId);
      this.$store.commit(
        "updateCurrentPlayDetail",
        this.currentPlayList[currentIndex - 1]
      );
      this.getSongDetail(nextPlayId);
      console.log(this.currentPlayList)
    },


    
  }
};
</script>
<style lang="less">
.player {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  max-width: 420px;
  height: 100px;
  background: #f1f3f4;
  // position: relative;
  p{
  text-align: left;
  margin:0 20px;
  }
  img{
    border: 1 solid none;
    border-radius: 50%;
  }
  .img{
    position: relative;
    top: 5px;
    height: 100%;
    img{
  width: 100%;
  // height: 100%;
}
  }
}

audio{
  position: absolute;
  right: 0;
  bottom: 0;
}
i{
  border: 1 solid black;
  border-radius: 50%;
}
.audio{
  position: absolute;
  bottom: 0px;
  right: 80px;
  img{
    width: 38px;
    // position: absolute;
    // bottom: -5px;
    // left: 0;
    // height: 100%;
  }
}
.Name{
  font-size: 20px;
  margin: 4px 0;
}
.el-row{
  height: 100%;
}
.el-col-18{
  height: 80%;
  overflow: hidden;
}
.el-col-6{
  height: 100%;
}
</style>