import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    fullscreenLoading: true,
    currentPlay: null,
    currentPlayDetail: null,
    currentPlayList: [],
    isplaying:true
  },
  mutations: {
    updateCurrentPlay: function (state, obj) {
      state.currentPlay = obj
    },
    updateCurrentPlayDetail: function (state, obj) {
      state.currentPlayDetail = obj
    },
    updateFullscreenLoadingFalse: function (state) {
      state.fullscreenLoading = false
    },
    updateFullscreenLoadingTrue: function (state) {
      state.fullscreenLoading = true
    },
    updataCurrentPlayList: function (state, array) {
      state.currentPlayList = array
    },
  },
  actions: {

  }
})